video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
  }
  
  .hero-container {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
  }

  .hero-container > h1 {
      color: white;
      font-size: 80px;
      margin-top: -100px;
  }

  .hero-container > p{
      margin-top: 8px;
      font-size: 32px;
      color: #ffffff;
      margin-bottom: 20px;
  }

.hero-btns {
    margin-top: 32px;
}

.hero-btns .btn {
    margin: 6px;
}

.fa-play-circle{
    margin-left: 4px;
}

@media screen and (max-width: 960px){
    .hero-container > h1 {
        font-size: 60px;
        margin-top: -150px;
    }
    video{
        display: none;
    }
    .hero-container{
        background: url('/images/img-5.jpg') center center/cover no-repeat; 

    }
}

@media screen and (max-width: 768px){
    .hero-container > h1{
        font-size: 35px;
        margin-top: -100px;
    }

    .hero-container > p {
        font-size: 12px;
    }
    .btn-mobile{
        display: block;
        text-decoration: none;
    }
    .btn {
        width: 100%;
    }
}
